<template>
  <div class="calendar-container">
    <div class="search-form">
      <Form ref="formValidate" :model="searchData" inline :label-width="40">
        <FormItem label="日期" prop="startTime" >
          <DatePicker size="large" v-model="searchData.startTime" type="date" placeholder="开始时间" style="width: 200px"></DatePicker>
        </FormItem>
        <FormItem label="—" class="date-line" prop="endTime" :label-width="20">
          <DatePicker size="large" v-model="searchData.endTime" type="date" placeholder="结束时间" style="width: 200px"></DatePicker>
        </FormItem>
        <FormItem :label-width="20">
          <Button size="large" type="primary" @click="search()">查询</Button>
          <Button size="large" @click="reset()" style="margin-left:8px">重置</Button>
          <Button size="large" type="success" icon="md-add-circle" style="margin-left:8px" @click="batchAdd()">编辑课时</Button>
          <!-- <Button size="large" type="warning" icon="ios-search" style="margin-left:8px" @click="classText()">课段文字</Button> -->
          <Button size="large" type="success" icon="ios-cloud-download-outline" style="margin-left:8px" @click="downImage()">下载图片</Button>
        </FormItem>
      </Form>
    </div>
    <div ref="calendar" class="calendar cl">
      <div class="week-title">
        <div :class="['weeks',{'weekend':i===0||i===6}]" v-for="(week,i) in weeks" :key="i">星期{{week}}</div>
      </div>
      <div ref="classes" class="classes-content cl">
        <div :class="['class',{'weekend':i===1||i===7}]" v-for="i in suppleCount.start" :key="i+'start'"></div>
        <div :class="['class','has-date',{'weekend':GW(d.date)===0||GW(d.date)===6,'today': GT()===d.date}]" v-for="d in dealClasses" :key="d.date">
          <h4 @click="editDateClasses(d)">{{d.date}}({{weeks[GW(d.date)]}})</h4>
          <div :style="`height:${dayHeight}px`" class="content">
            <div  @click="editDateStudent(c)" :style="`height:${dayHeight/(d.classes.length||1)}px;line-height:${dayHeight/(d.classes.length||1)}px`" class="item" v-for="c in d.classes||[]" :key="c.id">
              <span class="w-45">{{dealPeriods(c)}}</span>
              <span class="w-30">{{c.studentName}}</span>
              <span class="w-25">{{subjectMaps[c.subject]}}</span>
            </div>
          </div>
        </div>
        <div :class="['class',{'weekend':i===6-suppleCount.end}]" v-for="i in 6-suppleCount.end" :key="i+'end'"></div>
      </div>
      <div v-if="printTime" class="time-watermark">
        <p>{{printTime}}</p>
        <p>课时数：{{hourNumber.count}}</p>
        <p>小时数：{{hourNumber.hour}}</p>
      </div>
    </div>
    <add-classes :edit="addModalOptions.edit" :search="addModalOptions.search" :date.sync="addModalOptions.date" :classes.sync="addModalOptions.classes" :studentId.sync="addModalOptions.studentId" :modal.sync="addModal"></add-classes>
    <Modal :width="800" @on-cancel="textModalCancel" class="text-classes-modal" v-model="textModal" title="文字版课时">
      <div class="text-content">
        <div class="switch">
          <span>只需要没有周末的：</span>
          <i-switch v-model="textSwitch" @on-change="textSwitchChange"></i-switch>
          <span style="margin-left:10px">只需要展示空段：</span>
          <i-switch v-model="isEmptySwitch" @on-change="textSwitchChange"></i-switch>
          <span style="margin-left:10px">只需要满足排课条件的：</span>
          <i-switch v-model="isMatchTimes" @on-change="textSwitchChange"></i-switch>
        </div>
        <Input ref="classText" v-model="classesVlaue" :autosize="{ minRows: 24, maxRows: 24 }" type="textarea" :rows="10" />
      </div>
      <div slot="footer">
        <Button type="primary" @click="textModalEnter()">复 制</Button>
    </div>
    </Modal>
  </div>
</template>

<script>
import { dates, students, subjects, classes } from '../../mixins/index.js'
import html2canvas from 'html2canvas'
export default {
  mixins: [dates, students, subjects, classes],
  data () {
    return {
      dayHeight: 280,
      searchData: {
        startTime: '',
        endTime: '',
        name: ''
      },
      suppleCount: {
        start: 0,
        end: 0
      },
      classes: [],
      addModal: false,
      addModalOptions: {
        search: false,
        edit: false,
        date: '',
        classes: [],
        studentId: ''
      },
      textModal: false,
      textSwitch: false,
      isEmptySwitch: false,
      isMatchTimes: false,
      classesVlaue: '',
      printTime: ''
    }
  },
  computed: {
    dealClasses () {
      for (const d of this.classes) {
        d.classes = []
        for (const c of this.allClassData) {
          if (d.date === c.date) {
            d.classes.push(c)
          }
          d.classes = d.classes.sort((x, y) => {
            return x.startTime - y.startTime
          })
        }
      }
      return this.classes
    },
    hourNumber () {
      let hour = 0
      let count = 0
      let { startTime, endTime } = this.searchData
      let dates = this.GA(startTime, endTime)
      for (let item of this.allClassData) {
        if (dates.indexOf(item.date) >= 0) {
          hour += this.GTM(item)
          count++
        }
      }
      return { hour: parseInt(hour / 60), count }
    }
  },
  created () {
    this.initSearch()
    this.initTableByDate()
  },
  methods: {
    downImage () {
      let { startTime, endTime } = this.searchData
      startTime = this.MFD(startTime)
      endTime = this.MFD(endTime)
      this.printTime = this.GNT()
      this.$nextTick(() => {
        html2canvas(this.$refs.calendar).then((canvas) => {
          var imgUri = canvas.toDataURL('image/png').replace('image/png', 'image/octet-stream') // 获取生成的图片的url
          this.$lib.downloadFile(`${startTime}-${endTime}课表统计-${this.GN()}.png`, imgUri)
          this.printTime = ''
        })
      })
    },
    search () {
      let { startTime, endTime } = this.searchData
      if (startTime && endTime) {
        this.initTableByDate()
        return
      }
      let { s, e } = this.GABC()
      this.searchData.startTime = this.searchData.startTime || s
      this.searchData.endTime = this.searchData.endTime || e
      this.initTableByDate()
    },
    reset () {
      this.initSearch()
      this.initTableByDate()
    },
    batchAdd () {
      this.addModalOptions.edit = false
      this.addModalOptions.search = true
      this.addModal = true
    },
    classText () {
      this.textModal = true
      this.setTextData()
    },
    setTextData () {
      let classesVlaue = ''
      for (const item of this.classes) {
        if (this.textSwitch && (this.GW(item.date) === 0 || this.GW(item.date) === 6)) {
          continue
        }
        classesVlaue += `${item.date}(星期${this.weeks[this.GW(item.date)]})\r\n`
        if (item.classes && item.classes.length > 0) {
          let times = [...this.scheduleData]
          for (const c of item.classes) {
            if (!this.isEmptySwitch) {
              classesVlaue += this.dealPeriods(c) + '：' + c.studentName + '\r\n'
            }
            for (const period of c.periods) {
              let i = times.indexOf(period)
              times.splice(i, 1)
            }
          }
          let emptyList = this.timeSegmentation(times)
          for (const c of emptyList) {
            classesVlaue += c + '：空段' + '\r\n'
          }
        } else {
          classesVlaue += '无课时\r\n'
        }
        classesVlaue += '\r\n'
      }
      this.classesVlaue = classesVlaue
    },
    timeSegmentation (times) {
      let periods = [[times[0]]]
      times.reduce((pre, cur) => {
        if (this.FNT(pre) === cur) {
          periods[periods.length - 1].push(cur)
        } else {
          periods.push([cur])
        }
        return cur
      })
      if (this.isMatchTimes) {
        periods = periods.filter(item => {
          return item.length >= 3
        })
      }
      periods = periods.map(item => {
        item = `${item[0]}-${this.FNT(item[item.length - 1])}`
        return item
      })
      return periods
    },
    textModalCancel () {
      this.textModal = false
    },
    textModalEnter () {
      this.copyToClipboard()
      this.textModalCancel()
    },
    textSwitchChange () {
      this.setTextData()
    },
    copyToClipboard () {
      let classText = this.$refs.classText.$el
      let textarea = classText.getElementsByTagName('textarea')[0]
      textarea.select()
      textarea.setSelectionRange(0, 9999)
      if (document.execCommand('copy')) {
        document.execCommand('copy')
      }
      this.$Message.success('复制成功')
    },
    editDateClasses (date) {
      this.addModal = true
      this.addModalOptions.search = false
      this.addModalOptions.edit = true
      this.addModalOptions.date = date.date
      this.addModalOptions.classes = date.classes
    },
    editDateStudent (c) {
      this.addModal = true
      this.addModalOptions.search = false
      this.addModalOptions.edit = true
      this.addModalOptions.studentId = c.studentId
    },
    initSearch () {
      this.searchData.startTime = this.TPT()
      this.searchData.endTime = this.TFT()
    },
    initTableByDate () {
      let { startTime, endTime } = this.searchData
      let dates = this.GA(startTime, endTime)
      if (dates.length === 0) return
      this.suppleCount.start = this.GW(dates[0])
      this.suppleCount.end = this.GW(dates[dates.length - 1])
      this.classes = dates.map(item => {
        item = { date: item }
        return item
      })
    }
  }
}
</script>
